body {
  margin: 0;
}

* {
  box-sizing: border-box;
}

html {
  font-family: Roboto, sans-serif;
}

body, h1, p {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 120%;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo-link {
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  display: flex;
}

.header__logo {
  width: 100%;
  height: 100%;
}

.nav {
  justify-content: space-between;
  align-items: center;
  margin: auto 0;
  display: flex;
}

.nav__list {
  align-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  display: flex;
  position: relative;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: #060b35;
  cursor: pointer;
  text-align: left;
  align-items: center;
  margin: 0;
  padding: 23px 0;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__link.is-active {
  color: #00acdc;
  text-decoration: none;
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition-duration: .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__product-name {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-name:hover {
  color: #00acdc;
}

.card__product-info {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__review {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__review-text {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__price-block {
  color: #616070;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  font-size: 12px;
  line-height: 18px;
  display: flex;
}

.card__btn {
  color: #fff;
  text-transform: uppercase;
  background: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  max-width: 100%;
  padding: 12px 70px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  display: inline-flex;
}

.card__btn:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.main {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

